import { BrowserRouter as Router, Route, useNavigate, Navigate, Routes } from 'react-router-dom';
import { Security, SecureRoute, LoginCallback, useOktaAuth } from '@okta/okta-react';
import oktaConfig from './oktaConfig';
import { OktaAuth } from '@okta/okta-auth-js';
import Dashboard from './Dashboard';
import Home from './Home';

// Add this auth wrapper component

const oktaAuth = new OktaAuth(oktaConfig);

const RequireAuth = ({ children }) => {
  const { authState } = useOktaAuth();
  
  if (!authState?.isAuthenticated) {
    return <Navigate to="/" replace />;
  }
  
  return children;
};
function App() {
  // const navigate = useNavigate();

  return (
    <Router>
      <Security
        oktaAuth={oktaAuth}
        restoreOriginalUri={async (_oktaAuth, originalUri) => {
          // navigate(originalUri || '/', { replace: true });
        }}
      >
        <Routes>
          <Route path="/ndkn" element={<Home />} />
          <Route path="/ndkn/login/callback" element={<LoginCallback />} />
          <Route path="/ndkn/dashboard" element={
            <RequireAuth>
              <Dashboard />
            </RequireAuth>
          } />
        </Routes>
      </Security>
    </Router>
  );
}


export default App;