import { useOktaAuth } from '@okta/okta-react';
import LoginButton from './components/LoginButton';

const Home = () => {
  const { authState } = useOktaAuth();

  if (!authState) {
    return <div>Loading-----...</div>;
  }

  return (
    <div>
      {authState.isAuthenticated ? (
        <p>Welcome back!</p>
      ) : (
        <LoginButton />
      )}
    </div>
  );
};

export default Home;