import { useOktaAuth } from '@okta/okta-react';
import LogoutButton from './components/LogoutButton';

const Dashboard = () => {
  const { authState } = useOktaAuth();

  if (!authState?.isAuthenticated) {
    return <div>Loading...</div>;
  }

  return (
    <div>
      <h1>Dashboard</h1>
      <LogoutButton />
    </div>
  );
};

export default Dashboard;